._comment-controls {
  width: 100%;
  border-radius: 4px;
  padding: 4px 6px;
  border: 1px solid #e5e5e5;
  transition: all linear .2s;
}
._comment-content {
  border-top: 1px solid #ddd;
  padding: 10px;
}
._comment-reply {
  margin-bottom: 10px;
  color: #999;
}
.uk-comment-footer {
  text-align: right;
  margin: 10px 0;
}
.uk-text-overflow {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.images-center {
  /*非IE的主流浏览器识别的垂直居中的方法*/
  display: table-cell;
  vertical-align: middle;
  /*设置水平居中*/
  text-align: center;
  /* 针对IE的Hack */
  *display: block;
  *font-size: 175px;
  /*约为高度的0.873，200*0.873 约为175*/
  *font-family: Arial;
  /*防止非utf-8引起的hack失效问题，如gbk编码*/
  border: 1px solid #eee;
}
.images-center img {
  /*设置图片垂直居中*/
  vertical-align: middle;
  max-width: 100%;
  max-height: 100%;
}
.notes-footer {
  padding: 15px;
  background: #fafafa;
  color: #777;
  border-top: 1px solid #ddd;
}
.notes-footer .uk-subnav > li > a {
  color: #888;
}
#goToTop {
  display: none;
  position: fixed;
  right: 30px;
  bottom: 30px;
}
#goToBottom {
  display: none;
  position: fixed;
  right: 30px;
  bottom: 70px;
}
